.button {
    border: none;
    color: white;
    padding: 10px 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
  }
  
  .blue {background-color: #008CBA; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);}
  .red {background-color: #f44336; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);} 
  .white {background-color: #ffffff; color: black; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);}
  .yellow {background-color: #e7e55e; color: black; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);}
  .purple {background-color: #cc7ef6; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);}
  .green {background-color: #4CAF50; border:2px solid black; box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);}

  .pushed { border-radius: 50%; }

  .blue_select {background-color: #008CBA; border:2px solid black;}
  .red_select {background-color: #f44336; border:2px solid black;} 
  .white_select {background-color: #ffffff; color: black; border:2px solid black;}
  .yellow_select {background-color: #e7e55e; border:2px solid black;}
  .purple_select {background-color: #cc7ef6; border:2px solid black;}
  .green_select {background-color: #4CAF50; border:2px solid black;}