.button {
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  font-family:'Courier New', Courier, monospace;
  font-weight: bolder;
}

.blue {background-color: #008CBA;}
.red {background-color: #f44336;} 
.white {background-color: #ffffff; color: black; border:2px solid black;}
.yellow {background-color: #e7e55e;}
.purple {background-color: #cc7ef6;}
.green {background-color: #4CAF50;}